// 文字常量
import {
  BTN_TEXT, ENERGY, CONSTANTS_DEVICE_CONFIGURATION, EQUIPMENT_LIVE, EQUIPMENT_MONITORING, EQUIPMENT_DATA, MSG, PLC_PARAMS, VARIABLE, STATUS_CODE, FORM_CONFIG_DATE, IP, FILE_TYPE
} from '@/utils/constantsFile';
// 公共service
import { CommonService } from '@/services/common.service';
// API
import { getListMaintainTaskRelationshipItemById } from '@/api/equipment';
// message service
import { showWarningMessage } from '@/utils/message';
// 提示信息
import { showErrorNotify } from '@/utils/notify';
export default {
  name: 'ConfirmMaintainceModal',
  props: {
    title: { type: String, default: '' },
    showUser: { type: Boolean },
    cancelLabel: { type: String, default: BTN_TEXT.CLOSE },
    confirmLabel: { type: String, default: BTN_TEXT.CONFIRM_FINISH },
    showDialog: { type: Boolean, default: true },
    input: { type: String, default: '' },
    requiredData: { type: Object, default: () => { } },
    dataset: {
      type: Object,
      default: () => {
        return {
          isGrabMonad: 2,
          isChargeback: 2,
          isSave: 2,
          isSubmitReview: 2,
          isReject: 2,
          isRevoke: 2
        };
      }
    },
    tabs: { type: Array, default: [] },
    disableUpdate: { type: Boolean, default: false },
    handleCancel: Function,
    handleConfirm: Function,
    handleClose: Function,
    chargebackTask: Function,
    reviewMaintainTask: Function,
    rejectMaintainTask: Function,
    revokeMaintainTask: Function,
    takeTask: Function
  },
  data() {
    var setValidateForm = (rule, value, callback) => {
      return this.$options.filters.formatterFormValidate(rule, value, callback, this.extendPropertyValueVoList);
    };
    return {
      showModalFlag: false,
      setValidateForm: setValidateForm,
      VARIABLE: VARIABLE,
      FORM_CONFIG_DATE: FORM_CONFIG_DATE,
      PLC_PARAMS: PLC_PARAMS,
      BTN_TEXT: BTN_TEXT,
      MSG: MSG,
      EQUIPMENT_DATA: EQUIPMENT_DATA,
      EQUIPMENT_MONITORING: EQUIPMENT_MONITORING,
      EQUIPMENT_LIVE: EQUIPMENT_LIVE,
      CONSTANTS_DEVICE_CONFIGURATION: CONSTANTS_DEVICE_CONFIGURATION,
      ENERGY: ENERGY,
      // userId: window.localStorage.getItem('userId').replace(/"/g, ''),
      formLabelWidth: '135px',
      // 保养数据deep copy
      confirmData: null,
      confirmRules: {
        maintaince_start_ts: [
          { required: true, message: MSG.PLEASE_ENTER_CONTENT }],
        users: [
          { required: true, message: MSG.PLEASE_ENTER_CONTENT }],
        maintaince_end_ts: [
          { required: true, message: MSG.PLEASE_ENTER_CONTENT }]
      },
      // 扩展字段
      extendPropertyValueVoList: [],
      // requiredData: {},
      // 备注
      maintainRemark: '',
      // 表格加载标识
      tableLoading: false,
      // 保养项数据
      maintainceItems: {
        data: []
      },
      // 保养流程
      taskProcessRecordVoList: [],
      completeList: [
        { value: 1, label: '完成' },
        { value: 0, label: '未完成' }
      ],
      showForm: false,
      dialogImageUrl: '',
      changeFlag: true,
      fileList: [],
      delFileList: [],
      dialogVisible: false,
      uploadConfig: {
        headers: {
          token: window.localStorage.getItem('token').replace(/"/g, '')
        },
        url: IP.PUBLICIP + 'fileResource/uploadOneFile.action',
        data: {
          fileResourceTypeEnum: FILE_TYPE.MAINTAIN_IMAGE,
          relationshipId: ''
        }
      }
    };
  },
  methods: {
    change(e) {
      this.$forceUpdate();
    },
    /**
     * @description: 初始化窗口数据
     * @return: void
    */
    initData() {
      var position = '';
      if (this.requiredData.factory !== undefined) {
        if (this.requiredData.workshop !== undefined || this.requiredData.productionLine !== undefined) {
          position += this.requiredData.factory + '-';
        } else {
          position += this.requiredData.factory;
        }
      }
      if (this.requiredData.maintainState === 1) {
        this.changeFlag = false;
      } else {
        if (this.requiredData.maintainState === 4) {
          this.changeFlag = false;
        } else {
          this.changeFlag = true;
        }
      }
      if (this.requiredData.workshop !== undefined) {
        if (this.requiredData.productionLine !== undefined) {
          position += this.requiredData.workshop + '-';
        } else {
          position += this.requiredData.workshop;
        }
      }
      if (this.requiredData.productionLine !== undefined) {
        position += this.requiredData.productionLine;
      }
      this.taskProcessRecordVoList = this.requiredData.taskProcessRecordVoList;
      this.requiredData.position = position;
      this.fileList = [];
      this.delFileList = [];
      if (this.requiredData.users.length === 0 || this.requiredData.users === undefined) {
        this.requiredData.users = [];
        this.requiredData.users.push(window.localStorage.getItem('userId').replace(/"/g, ''));
      }

      // this.requiredData = CommonService.deepCopy(this.data);
      // this.requiredData.maintainRemark = '';
      // this.requiredData.users = this.requiredData.maintainUserIdList ? this.requiredData.maintainUserIdList : [];
      // this.requiredData.maintaince_start_ts = this.requiredData.startTime;
      // this.requiredData.maintaince_end_ts = this.requiredData.endTime;
      this.toExtendRulesAndShow(this.requiredData);
      this.getFileList(this.requiredData.maintainImageList);
      this.getListMaintainTaskRelationshipItemById();
      CommonService.getUsers();
    },
    /**
     * @description: 通过id获得关联的保养项
     * @return: void
    */
    getListMaintainTaskRelationshipItemById() {
      this.tableLoading = true;
      getListMaintainTaskRelationshipItemById({
        maintainId: this.requiredData.maintainId
      }).then(res => {
        this.tableLoading = false;
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          this.maintainceItems.data = res.rows;
        } else {
          showWarningMessage(res.message);
        }
      }).catch(err => {
        showErrorNotify(err);
      });
    },
    /**
     * @description: 处理点击取消
     * @return: void
    */
    cancelAction() {
      this.$refs.confirmDataForm.resetFields();
      this.fileList = [];
      if (this.handleCancel) {
        var self = this;
        setTimeout(() => {
          self.handleCancel();
        });
      }
    },
    /**
     * @description: 处理点击确认
     * @return: void
    */
    confirmAction(falg) {
      var data = this.requiredData;
      if (this.handleConfirm) {
        data.startTime = this.$options.filters.formatTime(this.requiredData.maintaince_start_ts);
        data.endTime = this.$options.filters.formatTime(this.requiredData.maintaince_end_ts);
        data.maintainUserIdList = this.requiredData.users;
        data.userIdList = this.requiredData.users.join(',');
        // this.requiredData.maintainRemark = this.maintainRemark;
        // this.confirmData.maintainState = this.confirmData.endTime > this.confirmData.maintainTime ? 3 : 2;
        data.maintainceItems = this.maintainceItems.data;
        data.flag = falg;
        data.maintainImageFileIds = this.fileList.map(item => {
          var json = '';
          if (!item.flag) {
          // if (item.response.rows.resourceId) {
            json = item.response.rows.resourceId;
          } else {
            json = item.id;
          }
          return json;
        });
        data.delFileList = this.delFileList;
        this.handleConfirm(data);
        this.clearData();
      }
    },
    /**
     * @description: 退单
     * @return: void
    */
    chargeback() {
      this.chargebackTask(this.requiredData.maintainId);
    },
    /**
    * @description: 通过
    * @return: void
    */
    pass() {
      this.reviewMaintainTask(this.requiredData.maintainId);
    },
    /**
    * @description: 退单
    * @return: void
    */
    reject() {
      this.rejectMaintainTask(this.requiredData.maintainId);
    },
    /**
    * @description: 退单
    * @return: void
    */
    revoke() {
      this.revokeMaintainTask(this.requiredData.maintainId);
    },
    /**
    * @description: 接单
    * @return: void
    */
    GrabMonad() {
      console.log(111);
      this.takeTask(this.requiredData.maintainId);
    },
    /**
     * @description: 判断是否能保存
     * @return: void
    */
    checkDataCorrect(flag) {
      this.$refs.confirmDataForm.validate((valid) => {
        if (valid) {
          this.confirmAction(flag);
        }
      });
    },
    /**
     * @description: 清空数据
     * @return: void
    */
    clearData() {
      this.maintainRemark = '';
      this.fileList = [];
      this.maintainceItems = {
        data: []
      };
    },
    /**
     * @description: 表单中扩展字段处理
     * @return: void
     */
    toExtendRulesAndShow(data) {
      // 保存扩展字段数据
      var arr = data.extendPropertyValueVoList.map((item) => {
        item.relationshipTypeId = data.maintainId;
        item.extendValue = item.extendValue ? item.extendValue : null;
        return item;
      });
      this.extendPropertyValueVoList = arr;
      var rules = JSON.parse(JSON.stringify(this.confirmRules));
      for (var item in this.extendPropertyValueVoList) {
        // 若类型是日期
        if (this.extendPropertyValueVoList[item].propertyType === 3) {
          rules[this.extendPropertyValueVoList[item].propertyCode] = [
            { validator: this.setValidateForm, trigger: ['blur', 'change'] }
          ];
        } else { // 若类型是字符或数字
          rules[this.extendPropertyValueVoList[item].propertyCode] = [
            { validator: this.setValidateForm, trigger: 'blur' }
          ];
        }
      }
      this.confirmRules = rules;
    },
    /**
     * @description: 图片控制
     * @param 无
     * @return:无
     * @author: ZJM
     */
    handleRemove(file, fileList) {
      if (!file.flag) {
        this.delFileList.push(file.response.rows.resourceId);
        this.fileList = fileList;
      } else {
        this.delFileList.push(file.id);
        this.fileList = fileList;
      }
    },
    handlePictureCardPreview(file) {
      this.dialogImageUrl = file.url;
      this.dialogVisible = true;
    },
    updateFiles() { },
    updateSuccess(response, file, fileList) {
      if (response.code === this.GLOBAL.STATUS_CODE.OPERATION_SUCCESS) {
        // this.dialogImageUrl = file.url;
        // this.dialogVisible = true;
        // this.fileList.push({
        //   id: response.rows.resourceId,
        //   url: IP.PUBLICIP + response.rows.fileUrl,
        //   relationshipId: response.rows.relationshipId
        // });
        this.fileList.push(file);
      } else {
        if (this.fileList.length === 0) {
          this.fileList = [];
        } else {
          this.fileList = JSON.parse(JSON.stringify(this.fileList));
        }
        showWarningMessage('上传异常');
      }
    },
    getFileList(imgFIle) {
      const arr = imgFIle.map(item => {
        var json = {
          id: item.resourceId,
          url: IP.PUBLICIP + item.fileUrl,
          relationshipId: item.relationshipId,
          flag: true
        };
        return json;
      });
      this.delFileList = [];
      this.fileList = arr;
    },
    formatterRealName({ cellValue }) {
      if (cellValue === '计划') {
        return '系统生成';
      } else {
        return cellValue;
      }
    }
  },
  watch: {
    requiredData(val, oldVal) {
      this.initData();
      if (this.showDialog) {
        this.showModalFlag = true;
      }
    },
    showDialog(val, oldVal) {
      if (!this.showDialog) {
        this.showModalFlag = false;
      }
    }
  },
  created() {
    // this.initData();
  }
};
