import maintainHistoryData from '../assets/json/maintanceTask/selectMaintainList.json';
import confirmMaintainData from '../assets/json/maintanceTask/confirmMaintain.json';
import moment from 'moment';
const MaintanceTaskService = {
  /**
   * @description: 处理保养任务数据
   * @param: {Array} data 保养任务元数据
   * @return: void
   */
  processMaintainData: function(data) {
    data.forEach(function(item) {
      item.mainLastTime = item.mainLastTime ? moment(item.mainLastTime).format('YYYY-MM-DD HH:mm:ss') : null;
      item.maintainTime = item.maintainTime ? moment(item.maintainTime).format('YYYY-MM-DD HH:mm:ss') : null;
      item.startTime = item.startTime ? moment(item.startTime).format('YYYY-MM-DD HH:mm:ss') : null;
      item.endTime = item.endTime ? moment(item.endTime).format('YYYY-MM-DD HH:mm:ss') : null;
    });
    return data;
  },
  // 获取保养记录
  getMaintainHistoryData: function(id) {
    var result = maintainHistoryData;
    return result;
  },
  // 确认保养
  confirmMiantain: function(data) {
    var result = confirmMaintainData;
    return result;
  },
  // process filters data
  generateFilters: function(data) {
    var filters = {};
    for (var key in data) {
      if (data[key]) {
        filters.hasFilter = true;
        filters[key] = data[key];
      }
    }
    return filters;
  }
};

export { MaintanceTaskService };
